
.vc-chrome {
  box-sizing: initial;
  width: 100%;
  background-color: #fff;
  background: #fff;
}
.vc-chrome-controls {
  display: flex;
}
.vc-chrome-sliders {
  flex: 1;
}
.vc-chrome-hue-wrap {
  position: relative;
  height: 10px;
  margin-bottom: 8px;
}
.vc-chrome-hue-wrap .vc-hue-picker, .vc-chrome-alpha-wrap .vc-alpha-picker {
  width: 20px;
  height: 20px;
  border-radius: 10px;
  transform: translate(-10px, -6px);
  background-color: rgb(248, 248, 248);
  box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.6);
}
.vc-chrome-body {
  padding: 10px 15px;
  background-color: #fff;
}
.vc-chrome-saturation-wrap {
  width: 100%;
  padding-bottom: 55%;
  position: relative;
  border-radius: 2px 2px 0 0;
  overflow: hidden;
}
.vc-chrome-saturation-wrap .vc-saturation-circle {
  width: 12px;
  height: 12px;
}
.vc-chrome__disable-alpha .vc-chrome-hue-wrap {
  margin-top: 4px;
  margin-bottom: 4px;
}



